import { Injectable } from '@angular/core';
import {ProductoRopa} from '../components/producto-ropa/interfaces';

@Injectable({
  providedIn: 'root'
})
export class CarritoService {

  constructor() { }

  public carrito:ProductoRopa[] = [];

  public add(p:ProductoRopa) {
      this.carrito.push(p);
  }


  public get cantidad() : number {
      return this.carrito.length;
  }

}
